var self;
export default{
	onLoad() {
		self=this;
	},
	data(){
		return{
			username:"",
			password:""
			
		}
	},
	methods:{
		toWritinginfomation:()=>{
			
		},
		formSubmit:(e)=>{
			const app = getApp()
			console.log(app.globalData.ctx)
			self.username=e.detail.value.username;
			self.password=e.detail.value.password;
			uni.request({
				url: app.globalData.ctx+'/login',
				header:{
					'Content-type':'application/x-www-form-urlencoded'
				},
				method: 'post',
				data: {
					"username": e.detail.value.username,
					"password": e.detail.value.password,
					"validateCode" : null,
					"rememberMe": false
				},
				success: res => {
					console.log(res)












					let cookies=res.cookies[0].split(";")[0];
					console.log(cookies)
					uni.setStorage({
					    key: 'cookies',
					    data: cookies,
					    success: function () {
					        console.log('获取cookies成功');
					    }
					});

					
					
					if(res.data.code==0){
						uni.reLaunch({
							url: '../index/index',
							animationType:"pop-in",
							animationDuration:300,
							success: res => {
								console.log("企业账户填写完成，页面跳转")
								uni.showToast({
								    title: '登陆成功',
									icon:'success'
								});
								setTimeout(function(){
									uni.hideToast();
								},1000)
							}
						});
					}else{
						uni.showToast({
						    title: '用户不存在或密码错误',
							icon:'none'
						});
						setTimeout(function(){
							uni.hideToast();
						},1000)
					}
				},
				fail: () => { 
					console.log("数据请求错误")
					uni.showToast({
					    title: '数据请求错误',
						icon:'none'
					});
					setTimeout(function(){
						uni.hideToast();
					},1000)
				},
				complete: () => {}
			});
		}
	}
}
